'use client';

import styles from './listing.module.css';
import Image from 'next/image';
import React, { useEffect, useState } from 'react';
import Link from 'next/link';

// Importe suas imagens
import logo1 from '@/images/fotos/logotipo_1.gif';
import logo2 from '@/images/fotos/logotipo_2.gif';
import logo3 from '@/images/fotos/logotipo_1.gif';
import logo4 from '@/images/fotos/logotipo_4.gif';
import logo5 from '@/images/fotos/logotipo_5.gif';
import logo6 from '@/images/fotos/logotipo_6.gif';
import logo7 from '@/images/fotos/logotipo_7.gif';
import logo8 from '@/images/fotos/logotipo_8.gif';
// ... importe as outras imagens

interface Servico {
    id?: number;
    titulo: string;
    nome: string;
    preco?: number; // Tornando opcional
}

export default function Listing() {
    const [servicos, setServicos] = useState<Servico[]>([]);
    const [loading, setLoading] = useState(true);
    const [error, setError] = useState<string | null>(null);

    useEffect(() => {
        const fetchServicos = async () => {
            try {
                const response = await fetch('http://localhost/dashboard/api-designOdyssey/servicos/index.php');

                if (!response.ok) {
                    throw new Error(`Erro HTTP: ${response.status}`);
                }

                const data = await response.json();

                // Validação dos dados
                const servicosValidados = Array.isArray(data) ? data.map(servico => ({
                    titulo: servico.titulo || 'Título não disponível',
                    nome: servico.nome || 'Anunciante desconhecido',
                    preco: Number(servico.preco) || 0, // Garante que preco seja número
                })) : [];

                setServicos(servicosValidados);
                setError(null);
            } catch (error) {
                console.error('Erro ao buscar serviços:', error);
                setError('Não foi possível carregar os serviços. Tente novamente mais tarde.');
            } finally {
                setLoading(false);
            }
        };

        fetchServicos();
    }, []);

    // Mapeamento de imagens
    const logos = [logo1, logo2, logo3, logo4, logo5, logo6, logo7,logo];

    if (loading) {
        return <div className={styles.main}>Carregando serviços...</div>;
    }

    if (error) {
        return <div className={styles.main}>{error}</div>;
    }

    return (
        <div className={styles.main}>
            <div className={styles.top}>
                <h1>Design de Logotipo</h1>
                <p>Deixe-nos ajudar a achar o serviço de Designer certo para você!</p>
            </div>
            <div className={styles.contentMain}>
                <div className={styles.contentBoxes}>
                    {servicos.map((servico, index) => (
                        <div key={index} className={styles.contentBox}>
                            <Image
                                src={logos[index % logos.length]}
                                height={300}
                                width={300}
                                alt={`logo-${index}`}
                            />
                            <h1>{servico.titulo}</h1>
                            <p>Anúncio de {servico.nome}</p>
                            {/* Adicionando verificação para preco */}
                            <span>A partir de R${servico.preco?.toFixed(2) || '0,00'}</span>
                        </div>
                    ))}
                </div>
            </div>
        </div>
    );
}